$month: 30;
$daySize: 100px;
$gap: 1px;
$calendarPadding: 10px;
$calendarSize: calendarWidth($daySize, $gap, $calendarPadding);
$startDays: (
  "mon" 0 * ($daySize + $gap),
  "tue" 1 * ($daySize + $gap),
  "wed" 2 * ($daySize + $gap),
  "thu" 3 * ($daySize + $gap),
  "fri" 4 * ($daySize + $gap),
  "sat" 5 * ($daySize + $gap),
  "sun" 6 * ($daySize + $gap),
);
